<template>
  <v-img
    :height="imageHeight"
    gradient="to top right, rgba(0,0,0,.33), rgba(0,0,0,.6)"
    :src="require('@/assets/about-us2.webp')"
  >
    <v-card
      class="d-flex flex-column mx-auto mt-6 px-2"
      min-height="500"
      max-width="1200"
    >
      <v-row>
        <v-col
          :xs="12"
          :sm="12"
          :md="6"
          :class="{ 'px-12': $vuetify.breakpoint.mdAndUp }"
        >
          <v-card-title
            class="headline"
            :class="{ 'pt-10': $vuetify.breakpoint.mdAndUp }"
          >
            Contact Us
          </v-card-title>
          <v-card-text class="pb-0">
            <div>
              Feel like contacting us? Submit your inquiries
              {{ $vuetify.breakpoint.mdAndUp ? "here" : "below" }} and our team
              will get back to you as soon as poosible.
            </div>
            <br />
            <div>
              Office: 604-275-9393
            </div>
            <div>
              Fax: 604-275-0454
            </div>
            <div>
              Toll Free: 1-800-811-3250
            </div>
            <br />
            <div>
              6061 Number 3 Rd #200 <br />
              Richmond, BC V6Y 2B2
            </div>
          </v-card-text>
          <div class="mt-4 text-center" style="overflow-hidden;">
            <div v-if="loading.map">
              <v-progress-circular
                class="mt-12"
                color="primary"
                size="50"
                indeterminate
              />
              <div class="mt-3">Loading map..</div>
            </div>
            <div v-show="!loading.map" class="mapouter">
              <div class="gmap_canvas">
                <iframe
                  :width="mapWidth"
                  :height="mapHeight"
                  id="gmap_canvas"
                  src="https://maps.google.com/maps?q=vancouver%20global%20shipping%2C%20richmond&t=&z=17&ie=UTF8&iwloc=&output=embed"
                  frameborder="0"
                  scrolling="no"
                  marginheight="0"
                  marginwidth="0"
                  @load="onLoadMap()"
                ></iframe>
              </div>
            </div>
          </div>
        </v-col>
        <v-col
          :xs="12"
          :sm="12"
          :md="6"
          :class="{ 'pt-10': $vuetify.breakpoint.mdAndUp }"
        >
          <v-card-title
            class="headline"
            :class="{ 'pt-10': $vuetify.breakpoint.mdAndUp }"
          >
            <span v-if="$vuetify.breakpoint.smAndDown">
              Inquiry
            </span>
          </v-card-title>
          <v-card-text class="pt-2">
            <ContactForm />
          </v-card-text>
        </v-col>
      </v-row>
    </v-card>
  </v-img>
</template>
<script>

import ContactForm from './ContactForm'

export default {
  components: {
    ContactForm,
  },
  data: () => ({
    loading: {
      map: true,
    },
  }),
  computed: {
    mapWidth() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': return "350px"
        case 'sm': return "600px"
        case 'md': return '600px'
        case 'lg': return '600px'
        case 'xl': return '600px'
        default: return '600px'
      }
    },
    mapHeight() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': return '400px'
        case 'sm': return '400px'
        case 'md': return '400px'
        case 'lg': return '400px'
        case 'xl': return '400px'
        default: return '400px'
      }
    },
    imageHeight() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': return null
        case 'sm': return null
        case 'md': return '900px'
        case 'lg': return '900px'
        case 'xl': return '900px'
        default: return '700px'
      }
    },
  },
  methods: {
    onLoadMap() {
      this.loading.map = false
    },
  },
  mounted() {
    let linkedinScript = document.createElement('script')
    linkedinScript.defer = true
    linkedinScript.setAttribute('src', 'https://platform.linkedin.com/badges/js/profile.js')
    document.head.appendChild(linkedinScript)
  }
}
</script>

<style scoped>
.bottom-border {
  border-bottom: 1px solid rgba(0, 0, 0, 0.12);
}

.mapouter {
  position: relative;
}
.gmap_canvas {
  overflow: hidden;
  background: none !important;
}
</style>

