<template>
  <v-form ref="form">
    <v-row>
      <v-col class="py-0" :cols="12" :sm="12" :xs="12" :md="12" :lg="6" :xl="6">
        <div class="caption font-weight-medium">FULL NAME</div>
        <v-text-field
          v-model="form.fullName"
          :rules="[rules.required]"
          dense
          outlined
        />
      </v-col>
      <v-col class="py-0">
        <div class="caption font-weight-medium">PHONE NUMBER</div>
        <v-text-field
          v-model="form.phoneNumber"
          :rules="[rules.required]"
          dense
          outlined
        />
      </v-col>
    </v-row>
    <v-row>
      <v-col class="py-0" :cols="12" :sm="12" :xs="12" :md="12" :lg="6" :xl="6">
        <div class="caption font-weight-medium">COMPANY NAME</div>
        <v-text-field
          v-model="form.companyName"
          :rules="[rules.required]"
          dense
          outlined
        />
      </v-col>
    </v-row>
    <v-row>
      <v-col class="py-0" :cols="12" :sm="12" :xs="12" :md="12" :lg="6" :xl="6">
        <div class="caption font-weight-medium">EMAIL</div>
        <v-text-field
          v-model="form.email"
          :rules="[rules.required, rules.email]"
          dense
          outlined
        />
      </v-col>
      <v-col class="py-0" :cols="12" :sm="12" :xs="12" :md="12" :lg="6" :xl="6">
        <div class="caption font-weight-medium">SERVICE REQUIRED</div>
        <v-select
          v-model="form.service"
          :items="services"
          :rules="[rules.required]"
          item-text="name"
          item-value="name"
          dense
          outlined
        />
      </v-col>
    </v-row>
    <v-row>
      <v-col class="py-0">
        <div class="caption font-weight-medium">MESSAGE</div>
        <v-textarea
          v-model="form.message"
          :rules="[rules.required]"
          dense
          outlined
        />
      </v-col>
    </v-row>
    <v-row>
      <v-btn
        class="px-10 ml-3"
        :color="sent ? 'success' : 'primary'"
        :loading="isSending"
        rounded
        @click="send"
      >
        <template v-if="sent">
          <v-icon>done</v-icon>
          Sent
        </template>
        <template v-else>
          Send
        </template>
      </v-btn>
    </v-row>
  </v-form>
</template>

<script>
import axios from 'axios'
const baseUrl = 'http://vancouverglobalshipping.com/WebsiteFormEmailer'
const URL = `${ baseUrl }`

export default {
  data: () => ({
    sent: false,
    isSending: false,
    form: {
      type: 0,
      fullName: '',
      phoneNumber: '',
      companyName: '',
      email: '',
      service: 'Ocean Freight',
      message: '',
    },
    services: [
      { name: 'Ocean Freight', href: 'ocean-freight' },
      { name: 'Automobile Shipping', href: 'automobile-shipping' },
      { name: 'Air Freight', href: 'air-freight' },
      { name: 'Import Services', href: 'import-services' },
    ],
    rules: {
      required: (v) => !!v || 'Required',
      email: (v) => !v || /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'E-mail must be valid'
    },
  }),
  methods: {
    async send() {
      if (!this.$refs.form.validate()) {
        console.log('Form invalid')
        return
      }

      this.isSending = true

      try {
        let response = await axios.post(URL, this.form)

        if (!response.data.err) {
          this.sent = true
          console.log(response)
        } else {
          console.error(response.data.err)
        }
      } catch (err) {
        console.error(err.toString());
      }

      this.isSending = false
    },
  },
  mounted() {
    let href = this.$route.query.service
    let service = this.services.find(s => s.href === href)

    if (service) this.form.service = service.name
  }
}
</script>  